.container{
  width: 99%;
  height: 99%;
  display: flex;
  flex-direction: column;
  // justify-content: center;
  align-items:center;
}

.wrapperTextHome{
   display: flex;
  flex-direction: column;
  align-items:center;
  min-width: 295px;
  margin-top: 40px;
  font-size: 0.9em;
  font-weight: 300;
}
.containerforTextLogin{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #FCD535;
  width: 80%;
  padding: 12px;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.8); /* Полупрозрачный белый фон */
  color: #000000; /* Чёрный текст по умолчанию */
  text-align: center;
  font-weight: bold;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Лёгкая тень для контраста */
  max-width: 600px;
}

.textOfLogin{
  display: block;
  justify-content: left;
  font-size: 12px;
  width: 240px;
  font: inherit;
}
.textOfLogin > :first-child{
  margin-left:10px;
}
.textOfLogin > :nth-child(3){
  margin-right:50px;
}

.textOfVidio{
  min-width: 738px;
  margin-top: 60px;
}

.welkomeText{
 
  font-size: larger;
 font-size: larger;
}
.btnSignUp{
  width: 100px;
  height: 30px;
  display: flex;
  justify-content: space-around;
  align-items:center;
  margin-right: 15px;
  border: none;
  border-radius: 5px;
  background: #FCD535;
  text-align: center;
  font-weight: 550;
  transition: background-color 0.3s;
  cursor: pointer;
}
.btnSignUp:hover{
  background: #fcd435a7;
}


